            howToReact: "How to reach Lohagad : There are different ways by which one can reach Lohagad. As discussed earlier, you have to reach the base village called as 'Malawli'.By Public Transport : If you are planning to travel by public transport, you need to catch a train to Pune.(from Mumbai).On the way, Malawli is the next station after Lonavala.Make sure the train you catch stops at Malawli.Once you get down at Malawli Station,walk towards the end of the station, i.e. towards the Pune side.Once you reach the end, turn right.And keep walking, till you come across a patch of land - we shall call it 'Junction'.That's because you would find  shops, hotels and some houses too.You would also find an open space - courtyard on the left side.Its used as a  parking space.This is the Bhaje gaon.By Road : We came here by road - the route is even simpler take the Mumbai-Pune express highway, soon after passing through Lonavala, we started looking at signs for Malawli and also asking people at every turn, just to be sure.Soon, we made a turn to the right, i.e. after asking for directions to local people.We then crossed over the railway lines,near the Malawli station and reached the 'Junction' - Bhaje gaon."
